import React, { useState } from "react";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import DeleteConfirmation from "app/views/common/components/DeleteConfirmation";
import {
  faBook,
  faPencilAlt,
  faTrash,
} from "@fortawesome/free-solid-svg-icons";
import ModuleForm from "../ModuleForm";

function ModuleDetails({ module: sectionModule, onUpdate, deleteModule }) {
  const { name, id } = sectionModule;
  const [editMode, setEditMode] = useState(false);
  const [deleteMode, setDeleteMode] = useState(false);

  const toggleEditMode = () => {
    setEditMode(!editMode);
  };
  const toggleDeleteMode = () => {
    setDeleteMode(!deleteMode);
  };
  const handleDeleteModule = () => {
    deleteModule(id);
  };

  return (
    <div className="module-details">
      <DeleteConfirmation
        visible={deleteMode}
        onCancel={toggleDeleteMode}
        onOk={handleDeleteModule}
        message={`Are you sure you want to delete this module?`}
      />
      <div className="text-right p-1">
        <Button
          variant="warning"
          className="text-right"
          onClick={toggleEditMode}
        >
          <FontAwesomeIcon icon={faPencilAlt} className="mr-2" />
          Edit
        </Button>
        <Button
          variant="danger"
          className="text-right ml-2"
          onClick={toggleDeleteMode}
          disabled={deleteMode}
        >
          <FontAwesomeIcon icon={faTrash} className="mr-2" />
          Delete
        </Button>
      </div>

      {editMode ? (
        <ModuleForm mode="update" onUpdate={onUpdate} module={sectionModule} />
      ) : (
        <Card className="border-0">
          <Card.Body>
            <div className="text-muted">
              <div className="mt-2 mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faBook} className="mr-2" />
                  Name
                </strong>
                <div>{name || "Not specified"}</div>
              </div>
            </div>
          </Card.Body>
        </Card>
      )}
    </div>
  );
}

ModuleDetails.propTypes = {};

export default ModuleDetails;
