import React, { useState } from "react";
import PropTypes from "prop-types";
import Card from "react-bootstrap/Card";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBook, faSave, faPlus } from "@fortawesome/free-solid-svg-icons";

function ModuleForm({ mode, module: sectionModule, onAdd, onUpdate }) {
  const [data, setData] = useState(
    mode === "add"
      ? {
          name: "",
        }
      : { ...sectionModule }
  );

  const handleChange = (field, e) => {
    e.stopPropagation();
    if (field) {
      const newValue = e.target.value;
      const oldValue = data[field];
      if (newValue !== oldValue) {
        setData({
          ...data,
          [field]: newValue,
        });
      }
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (mode === "add") onAdd(data);
    if (mode === "update") onUpdate(data);
  };

  const renderForm = () => {
    const { name } = data;
    return (
      <Form onSubmit={handleSubmit}>
        <Card className="border-0">
          <Card.Body>
            <div className="text-muted">
              <div className="mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faBook} className="mr-2" />
                  Name
                </strong>
                <Form.Group controlId="name" className="mb-0 mt-2">
                  <Form.Control
                    type="text"
                    value={name || ""}
                    placeholder="Enter name"
                    onChange={(e) => handleChange("name", e)}
                    required
                  />
                </Form.Group>
              </div>
            </div>
            <div className="pt-3">
              {mode === "add" && (
                <Button type="submit" variant="success">
                  <FontAwesomeIcon icon={faPlus} className="mr-2" />
                  Add Module
                </Button>
              )}
              {mode === "update" && (
                <Button type="submit" variant="success">
                  <FontAwesomeIcon icon={faSave} className="mr-2" />
                  Save Module
                </Button>
              )}
            </div>
          </Card.Body>
        </Card>
      </Form>
    );
  };

  return <div className="module-form">{renderForm()}</div>;
}

ModuleForm.propTypes = {
  mode: PropTypes.string,
  module: PropTypes.object,
  onAdd: PropTypes.func,
};

export default ModuleForm;
