import React from "react";
import Tab from "react-bootstrap/Tab";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Nav from "react-bootstrap/Nav";
import Button from "react-bootstrap/Button";
import ModuleForm from "./components/ModuleForm";
import ModuleDetails from "./components/ModuleDetails";

function ModulesComponent({
  localCourseModulesData,
  addModule,
  updateModule,
  deleteModule,
}) {
  return (
    <div id="cfg_modules" className="page p-0">
      <div className="p-3 bg-light">
        <h6 className="m-0">
          Module Count: <b>{localCourseModulesData.length}</b>
        </h6>
      </div>
      <div className="p-3">
        <Tab.Container id="left-tabs-example" defaultActiveKey="addNewModule">
          <Row>
            <Col xs={2} className="d-md-none">
              <Nav className="flex-column">
                <Nav.Item>
                  <Nav.Link
                    eventKey="addNewModule"
                    className="ellipsis clamp-1"
                  >
                    <Button variant="secondary">+</Button>
                  </Nav.Link>
                </Nav.Item>
                {localCourseModulesData.map((sectionModule, index) => (
                  <Nav.Item key={`module-${sectionModule.id}`}>
                    <Nav.Link eventKey={sectionModule.id}>
                      <Button variant="secondary">{index + 1}</Button>
                    </Nav.Link>
                  </Nav.Item>
                ))}
              </Nav>
            </Col>
            <Col md={3} xl={2} className="d-none d-md-block">
              <Nav variant="pills" className="flex-column">
                <Nav.Item>
                  <Nav.Link
                    eventKey="addNewModule"
                    className="ellipsis clamp-1"
                  >
                    Add new module
                  </Nav.Link>
                </Nav.Item>
                {localCourseModulesData.map((sectionModule, index) => (
                  <Nav.Item key={`module-${sectionModule.id}`}>
                    <Nav.Link
                      eventKey={sectionModule.id}
                      className="ellipsis clamp-2 position-relative"
                    >
                      {`${index + 1}. ${sectionModule.name}`}
                    </Nav.Link>
                  </Nav.Item>
                ))}
              </Nav>
            </Col>
            <Col xs={9} xl={10}>
              <Tab.Content>
                <Tab.Pane eventKey="addNewModule">
                  <ModuleForm mode="add" onAdd={addModule} />
                </Tab.Pane>
                {localCourseModulesData.map((sectionModule) => (
                  <Tab.Pane
                    eventKey={sectionModule.id}
                    key={`module-pane-${sectionModule.id}`}
                  >
                    <ModuleDetails
                      key={`module-${sectionModule.id}`}
                      module={sectionModule}
                      onUpdate={updateModule}
                      deleteModule={deleteModule}
                    />
                  </Tab.Pane>
                ))}
              </Tab.Content>
            </Col>
          </Row>
        </Tab.Container>
      </div>
    </div>
  );
}

ModulesComponent.propTypes = {};

export default ModulesComponent;
