import React, { useEffect, useState } from "react";
import ModulesComponent from "./ModulesComponent";
import { courseModulesOperations } from "app/store/state/active-course-modules";
import { useDispatch, useSelector } from "react-redux";
import "./modules.scss";

function Modules(props) {
  const activeCourseModulesData = useSelector(
    (state) => state.activeCourseModules.data
  );
  const dispatch = useDispatch();
  const [localCourseModulesData, setLocalCourseModulesData] = useState([]);
  const [activeCourseId, setActiveCourseId] = useState(props.match.params.id);
  const [isModified, setModifiedFlag] = useState(false);

  useEffect(() => {
    setModifiedFlag(false);
    setLocalCourseModulesData(activeCourseModulesData);
  }, [activeCourseModulesData]);

  useEffect(() => {
    const courseId = props.match.params.id;
    setActiveCourseId(courseId);
    dispatch(courseModulesOperations.loadModules(courseId));
    // eslint-disable-next-line
  }, [props.match.params.id]);

  const addModule = (module) => {
    dispatch(
      courseModulesOperations.addNewModuleDetails(activeCourseId, module)
    );
  };

  const updateModule = (module) => {
    dispatch(courseModulesOperations.saveModuleDetails(activeCourseId, module));
  };

  const deleteModule = (moduleId) => {
    dispatch(
      courseModulesOperations.deleteModuleDetails(activeCourseId, moduleId)
    );
  };

  return (
    <ModulesComponent
      localCourseModulesData={localCourseModulesData}
      isModified={isModified}
      addModule={addModule}
      updateModule={updateModule}
      deleteModule={deleteModule}
    />
  );
}

export default Modules;
